body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App .MyBox {
    fill: purple;
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.ELVIZ_percentage-label {
  font-style: italic;
}

.ELVIZ_tooltip {	
  position: absolute;			
  text-align: center;				
  display: inline-block;		
  padding: 4px;				
  font: 12px sans-serif;		
  background: white;	
  display: inline-block;
  border: 0px;		
  border-radius: 3px;			
  pointer-events: none;			
}

.ELVIZ_grouprect {	
  fill: #DEDEDE;
}

.ELVIZ_barchart.ELVIZ_stylesheet-tv {
  background: #1a1917;
  color: white;
  overflow: hidden;
}

.ELVIZ_barchart.ELVIZ_stylesheet-tv .ELVIZ_grouprect{
  fill: rgb(21, 73, 117);
}

.ELVIZ_barchart.ELVIZ_stylesheet-tv svg {
  color: white;
}
.ELVIZ_barchart.ELVIZ_stylesheet-tv text {
  fill: white;
  color: white;
}



/* barchart */

.ELVIZ_barchart {
  width: 100%;
  padding-top: 35%;
  position: relative;
}

.ELVIZ_barchart .ELVIZ_chart-title {
  font-weight: bold;
  text-align: center;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.ELVIZ_barchart .ELVIZ_chart-body {
  position: absolute;
  width: 100%;
  height: calc(100% - 20px);
  top: 20px;
  left: 0;
}

